$img: "/static/images/home-page/";

.home-page header {
  width: 100%;
  height: 0.97rem;
  padding: 0 0.4rem;
  background-color: #FFBF0D;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .location {
    width: 0.5rem;
    height: 100%;
    padding-top: 0.15rem;
    .icon {
      width: 100%;
      height: 0.45rem;
      background: url($img + "location.png") no-repeat center/auto 100%;
    }
    p {
      text-align: center;
      font-size: 0.22rem;
      color: #fff;
    }
  }

  .search {
    width: 5.26rem;
    height: 0.6rem;
    padding: 0 0.25rem;
    border-radius: 0.1rem;
    background-color: #eee;
    display: flex;
    align-items: center;
    .icon {
      width: 0.3rem;
      height: 0.3rem;
      background: url($img + "search.png") no-repeat center/100%;
      margin-right: 0.38rem;
    }
    input::-webkit-input-placeholder {
      color: red;
    }
  }

  .shopping-cart {
    width: 0.47rem;
    height: 0.44rem;
    background: url($img + "shopping-cart.png") no-repeat center/100%;
  }
}
